<template>
  <div id="app">
    <h1 id="gesture_name">Start</h1>
    <br>
    <div id="container" style="position: relative;">
      <video id="video" style="position: absolute; top:0; left: 0;"></video>
      <canvas id="canvas" style="position: absolute; top:0; left: 0;"></canvas>
    </div>
    <br>
  </div>
</template>

<script>
require('@tensorflow/tfjs');
require('@tensorflow/tfjs-backend-webgl');

import * as handpose from '@tensorflow-models/handpose'
import * as fp from 'fingerpose';
// import * as SerialPort from './src/bundle'
// import * as SerialPort from 'serialport'
// require('usb')
export default {
  name: 'App',
  components: {
  },
  async mounted() {
    // console.log(usb);
    // const usb = require('usb');
    // console.log(usb);
    const video = document.getElementById('video');
    video.style.transform = "rotateY(180deg)";
    const canvas = document.getElementById('canvas');
    canvas.style.transform = "rotateY(180deg)";

    // Printing Helper
    // var lastGesture = null;
    var currentGesture = null;
    // var lastReferencePoint = null;
    const gesture_name = document.getElementById('gesture_name');

    await navigator.getUserMedia(
      {
        video: true,
        audio: false
      }, async (stream) => {
        video.srcObject = stream;
        video.play();
      }, (err) => {
        console.log(err);
      })

    const net = await handpose.load();
    console.log('Handpose model loaded');

    const fingerJoints = {
    thumb: [0, 1, 2, 3, 4],
    indexFinger: [0, 5, 6, 7, 8],
    middleFinger: [0, 9, 10, 11, 12],
    ringFinger: [0, 13, 14, 15, 16],
    pinky: [0, 17, 18, 19, 20]
    };

    setInterval(async () => {
    gesture_name.textContent = "";
    if(video !== null && video.length !== 0) {

        const videoHeight = video.videoHeight;
        const videoWidth = video.videoWidth;

        video.height = videoHeight;
        video.width = videoWidth;

        canvas.height = videoHeight;
        canvas.width = videoWidth;

        const hand = await net.estimateHands(video);

        if(hand.length > 0) {
        const GE = new fp.GestureEstimator([
            fp.Gestures.VictoryGesture,
            fp.Gestures.ThumbsUpGesture,
            // fp.Gestures.ThumbsDownGesture,
            fp.Gestures.OpenHandGesture,
            fp.Gestures.ClosedHandGesture,
            fp.Gestures.IndexUpGesture,
            // fp.Gestures.UShapedUpGesture,
            fp.Gestures.ThumbsLeftGesture,
            fp.Gestures.ThumbsRightGesture,
            fp.Gestures.ThumbsDownGesture
        ])

        const gesture = await GE.estimate(hand[0].landmarks, 8);
        if (gesture.gestures !== undefined && gesture.gestures.length > 0) {
            const confidence = gesture.gestures.map(
            (prediction) => prediction.confidence
            );

            const maxConfidence = confidence.indexOf(
            Math.max.apply(null, confidence)
            );
            currentGesture = gesture.gestures[maxConfidence].name;

            const xmlRequest = new XMLHttpRequest();
            xmlRequest.open('GET', window.location.href + 'send/' + currentGesture, false);
            xmlRequest.send(null);
            console.log(xmlRequest.responseText);
            console.log(currentGesture);
            // gesture_name.textContent = currentGesture;
            // if(lastGesture == 'closed_hand') {
            // if(currentGesture == 'open_hand') {
            //     // port.write('tv on\n', (err) => {
            //     //   if (err) {
            //     //     return console.log('Error on write: ', err.message);
            //     //   }
            //     //   console.log('message written');
            //     // });
            // }
            // }

            // if(lastGesture == 'open_hand') {
            //   if(currentGesture == 'closed_hand') {
            //     //   port.write('tv off\n', (err) => {
            //     //   if (err) {
            //     //     return console.log('Error on write: ', err.message);
            //     //   }
            //     //   console.log('message written');
            //     // });
            //   }
            // }
            setTimeout(() => {}, 300);
            // lastGesture = currentGesture;
        }
        }

        const ctx = canvas.getContext("2d");
        if(hand.length > 0) {
        hand.forEach((prediction) => {
            const landmarks = prediction.landmarks;
            // console.log(landmarks[0]);

            // if(currentGesture == 'index_up') {
            //   if(lastGesture == 'index_up') {
            //     var vertical = Math.abs(landmarks[0][0] - lastReferencePoint[0]) < Math.abs(landmarks[0][1] - lastReferencePoint[1]); 
            //     if(vertical) {
            //       if(landmarks[0][0] < lastReferencePoint[0]) {
            //         console.log("volume up");
            //       } else {
            //         console.log("volume down");
            //       }
            //     }
            //   }
            // }

            // lastReferencePoint = landmarks[0];

            for(let j = 0; j< Object.keys(fingerJoints).length; j++) {
            let finger = Object.keys(fingerJoints)[j];
            for(let k = 0; k< fingerJoints[finger].length - 1; k++) {
                const firstJointIndex = fingerJoints[finger][k];
                const secondJointIndex = fingerJoints[finger][k + 1];

                ctx.beginPath();
                ctx.moveTo(
                landmarks[firstJointIndex][0],
                landmarks[firstJointIndex][1]
                );
                ctx.lineTo(
                landmarks[secondJointIndex][0],
                landmarks[secondJointIndex][1]
                );
                ctx.strokeStyle = 'plum';
                ctx.lineWidth = 4;
                ctx.stroke();
            }
            }

            for(let i = 0; i < landmarks.length; i++) {
            const x= landmarks[i][0];

            const y = landmarks[i][1];

            ctx.beginPath();
            ctx.arc(x, y, 5, 0, 3 * Math.PI);
            ctx.fillStyle = "indigo";
            ctx.fill();
            }
        })
        }
    }
    }, 300);
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>